* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

/* User */
p {
  margin: 0;
}

li {
  display: flex;
  align-items: center;
  gap: 10px;

  list-style: none;
}

ul {
  padding: 0;
}

span {
  font-weight: 700;
}

.country-info {
  display: flex;
  flex-direction: column;

  row-gap: 1px;
}

.content {
  align-items: center;
  display: flex;
  gap: 30px;
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 36px;
  line-height: 1.17;
  letter-spacing: 0.03em;
}

.list {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}
